import { NgModule } from '@angular/core';

import { TimeAgoPipe } from 'time-ago-pipe';
import { ViewsPipe } from './views.pipe';

@NgModule({
  declarations: [
    ViewsPipe,
    TimeAgoPipe,
  ],
  exports: [
    ViewsPipe,
    TimeAgoPipe,
  ],
  imports: [],
  providers: [],
})
export class PipesModule {
  static forRoot() {
      return {
          ngModule: PipesModule,
          providers: [],
      };
   }
}



