import * as Promise from 'bluebird';
import * as unirest from 'unirest';

import { Injectable } from '@angular/core';

import { environment } from '../../../environments/environment';
import { TrendingClips } from '../models/trending-clips';

interface Clip {}

@Injectable()
export class ClipsService {

  allClips: Map<string, TrendingClips> = new Map([]);

  getBySlug(slug: string): Promise<any> {
    let that = this;
    let url = environment.apiAddress + '/api/v1/clips/slug/' + slug;

    return new Promise(function(resolve, reject) {
       unirest.get(url)
        .headers({'Accept': 'application/json'})
        .send()
        .end(function(resp) {
          console.log(resp);

          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
    });
  }

  getTrendingInitial(): Promise<TrendingClips> {
    return this.getTrending(undefined);
  }

  getTrending(cursor: string|undefined): Promise<TrendingClips> {
    let clips: Array<Clip> = new Array<Clip>();
    let that = this;

    return new Promise(function(resolve, reject) {
      let url = environment.apiAddress + `/api/v1/clips/trending`;

      if (cursor) {
        url += `?cursor=${cursor}`;
      }

      unirest.get(url)
        .headers({'Accept': 'application/json'})
        .send()
        .end(function(resp) {

          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
    });
  }

  getTrendingByChannel(channel: string, cursor: string|undefined): Promise<TrendingClips> {
    let clips: Array<Clip> = new Array<Clip>();
    let that = this;

    return new Promise(function(resolve, reject) {
      let url = environment.apiAddress + `/api/v1/clips/trending/channel/${channel}`;

      if (cursor) {
        url += `?cursor=${cursor}`;
      }

      unirest.get(url)
        .headers({'Accept': 'application/json'})
        .send()
        .end(function(resp) {

          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
    });
  }

  getTrendingByGame(game: string, cursor: string|undefined): Promise<TrendingClips> {
    let clips: Array<Clip> = new Array<Clip>();
    let that = this;

    return new Promise(function(resolve, reject) {
      let url = environment.apiAddress + `/api/v1/clips/trending/game/${game}`;

      if (cursor) {
        url += `?cursor=${cursor}`;
      }

      unirest.get(url)
        .headers({'Accept': 'application/json'})
        .send()
        .end(function(resp) {

          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
    });
  }
}

export default new ClipsService();



