import * as Promise from 'bluebird';
import * as unirest from 'unirest';
import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { ChannelWrapper } from '../models/channel-wrapper.model';
import { UserWrapper } from '../models/user-wrapper';

@Injectable()
export class UsersService {

  getFollowingChannelsById(id: string): Promise<ChannelWrapper> {
    let that = this;
    let url = environment.apiAddress + `/api/v1/users/${id}/following`;

    return new Promise(function(resolve, reject) {
       unirest.get(url)
        .headers({'Accept': 'application/json'})
        .send()
        .end(function(resp) {

          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
    });
  }

  getUsers(login: string): Promise<UserWrapper> {
    let that = this;
    let url = environment.apiAddress + `/api/v1/users?login=${login}`;

    return new Promise(function(resolve, reject) {
       unirest.get(url)
        .headers({'Accept': 'application/json'})
        .send()
        .end(function(resp) {

          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
    });
  }
}
export default new UsersService();



