import { NgModule } from '@angular/core';
import { IonicModule } from 'ionic-angular';
import { ThumbnailComponent } from './thumbnail/thumbnail.component';
import { PipesModule } from '../pipes/pipes.module';
import { FollowingChannelThumbnailComponent } from './following-channel-thumbnail/following-channel-thumbnail.component';

@NgModule({
  declarations: [
    FollowingChannelThumbnailComponent,
    ThumbnailComponent
  ],
  exports: [
    FollowingChannelThumbnailComponent,
    ThumbnailComponent
  ],
  imports: [
    IonicModule,
    PipesModule
  ],
  providers: [],
})
export class ComponentModule {
  static forRoot() {
      return {
          ngModule: ComponentModule,
          providers: [],
      };
   }
}



