import { Pipe } from '@angular/core';
import { DecimalPipe } from '@angular/common';

@Pipe({
  name: 'views'
})
export class ViewsPipe extends DecimalPipe {
  transform(value: number): string {
  	let divider = 1;
    let format = "1.0-0";
  	let letter = '';
  	value = value || 0;

  	if (value > 1000000) {
  		divider = 1000000;
  		letter = 'M';
      format = '1.0-2';
  	} else if (value > 1000) {
  		divider = 1000;
  		letter = 'K';
  	}

    return super.transform((value / divider), format) + letter;
  }
}



