import { Thumbnails } from "./thumbnails.model";
import { Broadcaster } from "./broadcaster.model";
import { SafeHtml } from "@angular/platform-browser";

export class Clip {
	broadcaster: Broadcaster;
	createdAt: string;
	duration: number;
	embedHtml: SafeHtml;
	game: string;
	language: string;
	slug: string;
	thumbnails: Thumbnails;
	title: string;
	trackingId: string;
	url: string;
	views: number;
}



